.profile__title {
  text-rendering: optimizeLegibility;
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 500px;
}

@media all and (max-width: 700px) {
  .profile__title {
    font-size: calc(42px - (700px - 100vw) / 380 * 15);
    line-height: calc(48px - (700px - 100vw) / 380 * 15);
    max-width: calc(500px - (700px - 100vw) / 380 * (500 - 190));
  }
}
