.popup__submit-btn {
  color: white;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  font-family: "Inter", Arial, sans-serif;
  background-color: black;
  max-width: 358px;
  width: 100%;
  min-height: 50px;
  margin-top: 38px;
  margin-bottom: 36px;
  border: 0;
  padding: 0;
}

@media all and (max-width: 1280px) {
  .popup__submit-btn {
    max-width: calc(358px - ((1280px - 100vw) / 960 * (358 - 238)));
    font-size: calc(18px - ((1280px - 100vw) / 960 * (18 - 14)));
    line-height: calc(22px - ((1280px - 100vw) / 960 * (22 - 17)));
  }
}

.popup__submit-btn:not([disabled]):hover {
  opacity: 80%;
  cursor: pointer;
}
