.popup__container {
  max-width: 430px;
  width: 100%;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  opacity: 100%;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media all and (max-width: 1280px) {
  .popup__container {
    max-width: calc(430px - ((1280px - 100vw) / 960 * (430 - 282)));
  }
}
